      "।।9.18।। व्याख्या, [अपनी रुचि, श्रद्धा-विश्वासके अनुसार किसीको भी साक्षात् परमात्माका स्वरूप मानकर उसके साथ सम्बन्ध जोड़ा जाय तो वास्तव में यह सम्बन्ध सत्के साथ ही है। केवल अपने मन-बुद्धि में किञ्चिन्मात्र भी संदेह न हो। जैसे ज्ञानके द्वारा मनुष्य सब देश, काल, वस्तु व्यक्ति आदि में एक परमात्मतत्त्वको ही जानता है। परमात्माके सिवाय दूसरी किसी वस्तु, व्यक्ति, घटना, परिस्थिति, क्रिया, आदि की किञ्चिन्मात्र भी स्वतन्त्र सत्ता नहीं है, इसमें उसको किञ्चिन्मात्र भी संदेह नहीं होता। ऐसे ही भगवान् विराट्रूपसे अनेक रूपोंमें प्रकट हो रहे हैं अतः सब कुछ भगवान्हीभगवान् हैं, इसमें अपने को किञ्चिन्मात्र भी संदेह नहीं होना चाहिये। कारण कि यह सब भगवान् कैसे हो सकते हैं यह संदेह साधक को वास्तविक तत्त्वसे, मुक्तिसे वञ्चित कर देता है और महान् आफतमें फँसा देता है। अतः यह बात दृढ़ता से मान लें कि कार्य कारणरूपे स्थूलसूक्ष्मरूप जो कुछ देखने, सुनने, समझने और माननेमें आता है, वह सब केवल भगवान् ही हैं। इसी कार्य कारणरूपसे भगवान की सर्वव्यापकताका वर्णन सोलहवेंसे उन्नीसवें श्लोकतक किया गया है।]",
