    commentary:
      "।।9.27।। व्याख्या, भगवान का यह नियम है कि जो जैसे मेरी शरण लेते हैं, मैं वैसे ही उनको आश्रय देता हूँ (गीता 4। 11)। जो भक्त अपनी वस्तु मेरे अर्पण करता है, मैं उसे अपनी वस्तु देता हूँ। भक्त तो सीमित ही वस्तु देता है, पर मैं अनन्त गुणा करके देता हूँ। परन्तु जो अपने-आपको ही मुझे दे देता है, मैं अपनेआपको उसे दे देता हूँ। वास्तव में मैंने अपने-आपको संसारमात्रको दे रखा है (गीता 9। 4), और सबको सब कुछ करने की स्वतन्त्रता दे रखी है। अगर मनुष्य मेरी दी हुई स्वतन्त्रताको मेरे अर्पण कर देता है, तो मैं भी अपनी स्वतन्त्रताको उसके अर्पण कर देता हूँ अर्थात् मैं उसके अधीन हो जाता हूँ। इसलिये यहाँ भगवान् उस स्वतन्त्रताको अपने अर्पण करने के लिये अर्जुन से कहते हैं।]",
