    text: "शुभाशुभफलैरेवं मोक्ष्यसे कर्मबन्धनैः।\nसंन्यासयोगयुक्तात्मा विमुक्तो मामुपैष्यसि।।9.28।।",
    translation:
      "।।9.28।। इस प्रकार मेरे अर्पण करने से जिनसे कर्मबन्धन होता है, ऐसे शुभ (विहित) और अशुभ (निषिद्ध) सम्पूर्ण कर्मोंके फलोंसे तू मुक्त हो जायगा। ऐसे अपनेसहित सब कुछ मेरे अर्पण करने वाला और सबसे मुक्त हुआ तू मुझे प्राप्त हो जायगा।",
